import React from "react"
import { NavLink } from "react-router-dom";
import web from '../src/images/banner.jpg';


const Card = (props) => {
    return (
        <>

            <div className="row gy-4">
                <div className="col-md-4 col-10 mx-auto">
                    <div className="card">
                        <img className="card-img-top" src={props.imgsrc} alt="Card image cap" />
                        <div className="card-body">
                            <h5 className="card-title fw-bold text-info">{props.title}</h5>
                            <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                            <NavLink to="" className="btn btn-info" id="card-btn">
                                Let's start
                            </NavLink>
                        </div>

                    </div>
                </div>

            </div>
        </>
    );
};

export default Card;